import React from "react";
import "./statsCard.css";

export const StatsCard = ({ imgSrc, number, content }) => {
  return (
    <div className="statsCard">
      <div>
        <img src={imgSrc} alt="stats logo" style={{ maxWidth: "100%" }} />
      </div>
      <div style={{ paddingLeft: 20 }}>
        <span style={{ fontWeight: "bold", display: "block" }}>{number}</span>
        <span>{content}</span>
      </div>
    </div>
  );
};
