import React from "react";
import signature from "../../../assists/signature.png";
import "./aboutSection.css";

class AboutSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      image: React.createRef()
    };
  }

  componentDidMount() {
    this.io();
  }

  io = () => {
    let options = {
      rootMargin: "0px",
      threshold: 0.2
    };

    let targetEl = this.state.image.current;

    let imageObserver = new IntersectionObserver((entries, imageObserver) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          targetEl.classList.remove("sepia");
          imageObserver.unobserve(targetEl);
        }
      });
    }, options);

    imageObserver.observe(targetEl);
  };

  render() {
    return (
      <div className="aboutMeSection">
        <div
          ref={this.state.image}
          className="amelia sepia"
          style={{
            backgroundImage:
              "url(https://cdn.pixabay.com/photo/2016/03/23/04/01/beautiful-1274056_960_720.jpg)"
          }}
        ></div>
        <div className="aboutMeSection__info">
          <h3 style={{ marginBottom: 40 }}>About Me</h3>
          <p style={{ marginBottom: 40 }}>
            Given let waters air sea had you'll, may seed abundantly fish. Were,
            you'll earth forth winged above brought. Own darkness they're him
            can't fourth sea place have.
            <br />
            So the above May stars cattle fruitful face shall. Tree it, winged.
            Every signs male firmament us. Morning him.
          </p>

          <img src={signature} alt="the signture of the designer" />
        </div>
      </div>
    );
  }
}

export default AboutSection;
