import React from "react";
import { StatsCard } from "./StatsCard";
import "./stats.css";

export const Stats = ({ contents }) => {
  return (
    <div className="stats">
      {contents.map(content => (
        <StatsCard
          content={content.content}
          number={content.number}
          imgSrc={content.imgSrc}
          key={content.id}
        />
      ))}
    </div>
  );
};
