import React from "react";
import Banner from "../Common_Elements/Banner/Banner";
import { Stats } from "./Stats/Stats";
import Button from "../Common_Elements/Button";
import star from "../../assists/star.png";
import suitcase from "../../assists/suitcase.png";
import clock from "../../assists/clock.png";
import heart from "../../assists/heart.png";
import AboutSection from "./AboutSection/AboutSection";

const contents = [
  {
    imgSrc: suitcase,
    content: "Projects Completed",
    number: "435",
    id: 1
  },
  {
    imgSrc: clock,
    content: "Working Hours",
    number: "1232",
    id: 2
  },
  {
    imgSrc: star,
    content: "Positive Feedback",
    number: "643",
    id: 3
  },
  {
    imgSrc: heart,
    content: "Happy Clients",
    number: "847",
    id: 4
  }
];

const About = () => {
  return (
    <div style={{ backgroundColor: "#3e3e3e" }}>
      <Banner
        title={"Amelia Woods"}
        content={`I am a Graphic & Web Designer based in New York, specializing
in User Interface Design and Development.`}
        bgColor="transparent"
        contentColor="#ccc"
        headingColor="#fff"
      />

      <Stats contents={contents} />

      <AboutSection />

      <div style={{ backgroundColor: "#fff" }}>
        <Banner
          title={"Need a Project?"}
          content={`Let us know what you're looking for in an agency. We'll take a look and see
if this could be the start of something beautiful.`}
          bgColor="transparent"
          contentColor="#787878"
          headingColor="#1d1d1d"
        >
          <Button message={`Let's Talk`} />
        </Banner>
      </div>
    </div>
  );
};

export default About;
